import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { ActivatedRoute } from '@angular/router';
import { ToastController, IonRouterOutlet, NavController, LoadingController, AlertController } from '@ionic/angular';
import { ApiService } from 'src/app/_services/api.service';

@Component({
  selector: 'app-listlaporan',
  templateUrl: './listlaporan.page.html',
  styleUrls: ['./listlaporan.page.scss'],
  providers: [ApiService]
})
export class ListlaporanPage implements OnInit {

  title = 'Laporan ';

  params: any;
  subscribe: any;

  lists = [];

  geo_id: any;

  constructor(
    private route: ActivatedRoute,
    public NavController: NavController,
    private apiService: ApiService,
    public toastController: ToastController,
    public alertController: AlertController,
    private router: Router,
  ) {
    this.params = this.route.snapshot.params;
    // console.log(this.params);
    this.title = this.title + this.params.Type;

    this.geo_id = localStorage.getItem('geo_id');

  }

  ngOnInit() {

  }

  ionViewDidEnter() {
    this.getListLaporan();
  }

  async alertToRelogin() {

    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Warning !',
      message: 'Akun anda terdeteksi sedang digunakan diperangkat lain, mohon login kembali untuk mendapatkan akses Anda kembali',
      mode: "ios",
      backdropDismiss: false,
      buttons: [
        {
          text: 'Login Ulang',
          handler: () => {

            localStorage.removeItem('currentUser');
            localStorage.removeItem('geo_id');
            localStorage.removeItem('api_key');
            this.router.navigate(['front/']);

          }
        }
      ]
    });

    await alert.present();
  }

  getListLaporan() {
    this.subscribe = this.apiService.get('v2/user_areas_list?type=' + this.params.Type + '&geo_id=' + this.geo_id, {}).subscribe(
      data => {
        this.lists = data;
      },
      error => {
        if (error.status == 401) {
          this.alertToRelogin();
        }
      }
    );
  }

  selectTime() {
    this.NavController.navigateForward(['/laporan/selecttime', { Type: this.params.Type }]);
  }

  gotoSelectDusun(item: any) {

    this.subscribe = this.apiService.get('v2/user_areas_list_detail?glue=' + item.glue + '&geo_id=' + this.geo_id, {}).subscribe(
      data => {

        if (data.length > 0) {

          let ans_id_desa = '';
          let ans_id_dusun = '';
          let selectDesa = 0;
          let selectDesa_id_questionnaire = 0;
          let selectDusun = 0;
          let selectDusun_id_questionnaire = 0;
          let array = data;

          let label = '';
          let range = '';
          let value = '';

          for (let i = 0; i < array.length; i++) {
            const element = array[i];

            label = element.label;
            range = element.range;
            value = item.glue.substring(2, 20)

            if (element.target == '0') {
              ans_id_dusun = element.id;
              selectDusun = 1;
              selectDusun_id_questionnaire = element.id_questionnaire;
            }

            if (element.target == '1') {
              ans_id_desa = element.id;
              selectDesa = 1;
              selectDesa_id_questionnaire = element.id_questionnaire;
            }
          }

          let paramToDusun = {
            Type: item.type,
            ans_id_desa: ans_id_desa,
            ans_id_dusun: ans_id_dusun,
            checked: false,
            label: label,
            range: range,
            selectDesa: selectDesa,
            selectDesa_id_questionnaire: selectDesa_id_questionnaire,
            selectDusun: selectDusun,
            selectDusun_id_questionnaire: selectDusun_id_questionnaire,
            value: value,
            id_questionnaire: ''
          };

          this.NavController.navigateForward(['/laporan/selectdusun', paramToDusun]);

        }




      }
    );




  }

  goBack() {
    this.NavController.navigateBack(['/home'], { replaceUrl: true });
  }

}
