import { Component, OnInit } from '@angular/core';
import { ToastController, ActionSheetController, NavController, LoadingController, AlertController } from '@ionic/angular';

import { ApiService } from 'src/app/_services/api.service';


@Component({
  selector: 'app-datadasardesa',
  templateUrl: './datadasardesa.page.html',
  styleUrls: ['./datadasardesa.page.scss'],
  providers: [ApiService]
})
export class DatadasardesaPage implements OnInit {

  subscribe: any;

  listAPBDes = [];

  statusBasicInformasi = 0;

  constructor(
    public NavController: NavController,
    private apiService: ApiService,
  ) { }

  ngOnInit() {
  }

  ionViewDidEnter() {
    this.getBasicData();
    this.getListAPBDes();
  }

  getListAPBDes() {

    let geo_id = localStorage.getItem('geo_id');

    this.subscribe = this.apiService.get('v2/apbdes?geo_id=' + geo_id, {}).subscribe(
      data => {
        console.log(data);
        this.listAPBDes = data;
      },
      error => {
        if (error.status == 401) {
          // this.alertToRelogin();
        }
      }
    );
  }

  getBasicData() {

    let geo_id = localStorage.getItem('geo_id');

    this.subscribe = this.apiService.get('v2/basic_data?geo_id=' + geo_id, {}).subscribe(
      data => {
        console.log(data);
        if (data.length > 0) {
          this.statusBasicInformasi = 1;
        }
      },
      error => {
        if (error.status == 401) {
          // this.alertToRelogin();
        }
      }
    );
  }

  gotoDataDasarForm(item: any) {
    console.log(item);
    this.NavController.navigateForward(['/laporan/datadasardesaform', item]);
  }

  gotoInformasiDasarDesa() {
    this.NavController.navigateForward(['/laporan/informasidesa', []]);
  }

  goBack() {
    this.NavController.navigateBack(['/laporan/groups'], { replaceUrl: true });
  }

}
