import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastController, ActionSheetController, NavController, LoadingController, AlertController } from '@ionic/angular';

import { ApiService } from 'src/app/_services/api.service';

@Component({
  selector: 'app-datadasardesaform',
  templateUrl: './datadasardesaform.page.html',
  styleUrls: ['./datadasardesaform.page.scss'],
  providers: [ApiService]
})
export class DatadasardesaformPage implements OnInit {

  params: any;
  subscribe: any;

  listQuestions = [];

  constructor(
    public NavController: NavController,
    private route: ActivatedRoute,
    public toastController: ToastController,
    public loadingController: LoadingController,
    private apiService: ApiService,) {

    this.params = this.route.snapshot.params;

    console.log(this.params);

  }

  ngOnInit() {
  }

  ionViewDidEnter() {
    this.getListAPBDesDetail();
  }

  async presentLoading() {
    const loading = await this.loadingController.create({
      cssClass: 'my-custom-class',
      message: 'Mengirim data...',
      mode: 'ios',
      duration: 2000
    });
    await loading.present();

    const { role, data } = await loading.onDidDismiss();
    // console.log('Loading dismissed!');
  }

  async presentToast(color, header, message) {
    const toast = await this.toastController.create({
      header: header, //"Warning",
      message: message, //"Mohon isi semua formulir",
      color: color, //"danger",
      mode: "ios",
      position: "middle",
      duration: 3000
    });
    toast.present();
  }


  getListAPBDesDetail() {

    let geo_id = localStorage.getItem('geo_id');

    this.subscribe = this.apiService.get('v2/apbdes_details?geo_id=' + geo_id + '&apbdesa_head_id=' + this.params.id, {}).subscribe(
      data => {
        console.log(data);
        this.listQuestions = data;

      },
      error => {
        if (error.status == 401) {
          // this.alertToRelogin();
        }
      }
    );
  }

  kirimDataDasar() {

    this.presentLoading();

    let geo_id = localStorage.getItem('geo_id');

    let paramPost = {
      geo_id: geo_id,
      apbdesa_head_id: this.params.id,
      listQuestions: JSON.stringify(this.listQuestions)
    };

    this.subscribe = this.apiService.post('v2/apbdes_details', paramPost).subscribe(
      data => {
        // this.getListItems();
        setTimeout(() => {
          this.presentToast('success', 'Success', 'Data tersimpan');
          // this.NavController.navigateBack(['/basicdata/registrasidusun'], { replaceUrl: true });
        }, 2000);
      },
      error => {
        setTimeout(() => {
          this.presentToast('danger', 'Error', 'Data tidak tersimpan');
        }, 2000);
      }
    );

  }

  formatRupiah(angka) {

    if (angka != null && angka != '') {
      var number_string = parseFloat(angka).toString(),
        split = number_string.split(','),
        sisa = split[0].length % 3,
        rupiah = split[0].substr(0, sisa),
        ribuan = split[0].substr(sisa).match(/\d{3}/gi);

      // tambahkan titik jika yang di input sudah menjadi angka ribuan
      if (ribuan) {
        let separator = sisa ? '.' : '';
        rupiah += separator + ribuan.join('.');
      }

      rupiah = split[1] != undefined ? rupiah + ',' + split[1] : rupiah;
      return rupiah;
    }

  }

  goBack() {
    this.NavController.navigateBack(['/laporan/datadasardesa'], { replaceUrl: true });
  }

}
