import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


import { FormlaporanPage } from './formlaporan/formlaporan.page';
import { SelecttimePage } from './selecttime/selecttime.page';
import { SelectdusunPage } from './selectdusun/selectdusun.page';
import { ListlaporanPage } from './listlaporan/listlaporan.page';
import { ViewlaporanPage } from './viewlaporan/viewlaporan.page';
import { GroupsPage } from './groups/groups.page';
import { SelectperiodePage } from './selectperiode/selectperiode.page';
import { SelectdusunonlyPage } from './selectdusunonly/selectdusunonly.page';
import { DatadasardesaPage } from './datadasardesa/datadasardesa.page';
import { DatadasardesaformPage } from './datadasardesaform/datadasardesaform.page';
import { InformasidesaPage } from './informasidesa/informasidesa.page';

const routes: Routes = [
  {
    path: 'formlaporan',
    component: FormlaporanPage
  },
  {
    path: 'selecttime',
    component: SelecttimePage
  },
  {
    path: 'selectdusun',
    component: SelectdusunPage
  },
  {
    path: 'listlaporan',
    component: ListlaporanPage
  },
  {
    path: 'viewlaporan',
    component: ViewlaporanPage
  },
  {
    path: 'groups',
    component: GroupsPage
  },
  {
    path: 'selectperiode',
    component: SelectperiodePage
  },
  {
    path: 'selectdusunonly',
    component: SelectdusunonlyPage
  },
  {
    path: 'datadasardesa',
    component: DatadasardesaPage
  },
  {
    path: 'datadasardesaform',
    component: DatadasardesaformPage
  },
  {
    path: 'informasidesa',
    component: InformasidesaPage
  }



];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class LaporanPageRoutingModule { }
