import { Component, OnInit } from '@angular/core';
import { ToastController, ActionSheetController, NavController, LoadingController, AlertController } from '@ionic/angular';

import { ApiService } from 'src/app/_services/api.service';

@Component({
  selector: 'app-informasidesa',
  templateUrl: './informasidesa.page.html',
  styleUrls: ['./informasidesa.page.scss'],
  providers: [ApiService]
})
export class InformasidesaPage implements OnInit {

  subscribe: any;
  rt: any;
  rw: any;
  posko_desa: any;
  phone_number: any;
  telephone: any;

  constructor(
    public NavController: NavController,
    public toastController: ToastController,
    public loadingController: LoadingController,
    private apiService: ApiService,) { }

  ngOnInit() {
  }

  ionViewDidEnter() {
    this.getBasicData();
  }

  async presentLoading() {
    const loading = await this.loadingController.create({
      cssClass: 'my-custom-class',
      message: 'Mengirim data...',
      mode: 'ios',
      duration: 2000
    });
    await loading.present();

    const { role, data } = await loading.onDidDismiss();
    // console.log('Loading dismissed!');
  }

  async presentToast(color, header, message) {
    const toast = await this.toastController.create({
      header: header, //"Warning",
      message: message, //"Mohon isi semua formulir",
      color: color, //"danger",
      mode: "ios",
      position: "middle",
      duration: 3000
    });
    toast.present();
  }

  getBasicData() {

    let geo_id = localStorage.getItem('geo_id');

    this.subscribe = this.apiService.get('v2/basic_data?geo_id=' + geo_id, {}).subscribe(
      data => {
        console.log(data);
        if (data.length > 0) {
          let d = data[0];
          this.rt = d.rt;
          this.rw = d.rw;
          this.posko_desa = d.posko_desa;
          this.phone_number = d.phone_number;
          this.telephone = d.telephone;
        }
      },
      error => {
        if (error.status == 401) {
          // this.alertToRelogin();
        }
      }
    );
  }

  kirimDataDasar() {

    this.presentLoading();

    let geo_id = localStorage.getItem('geo_id');

    let paramPost = {
      geo_id: geo_id,
      rt: this.rt,
      rw: this.rw,
      posko_desa: this.posko_desa,
      phone_number: this.phone_number,
      telephone: this.telephone
    };

    this.subscribe = this.apiService.post('v2/basic_data', paramPost).subscribe(
      data => {
        // this.getListItems();
        setTimeout(() => {
          this.presentToast('success', 'Success', 'Data tersimpan');
          // this.NavController.navigateBack(['/basicdata/registrasidusun'], { replaceUrl: true });
        }, 2000);
      },
      error => {
        setTimeout(() => {
          this.presentToast('danger', 'Error', 'Data tidak tersimpan');
        }, 2000);
      }
    );

  }

  goBack() {
    this.NavController.navigateBack(['/laporan/datadasardesa'], { replaceUrl: true });
  }

}
