import { Component, OnInit } from '@angular/core';
import { async } from '@angular/core/testing';
import { Router, ActivatedRoute } from '@angular/router';

import { ToastController, ActionSheetController, NavController, LoadingController, AlertController } from '@ionic/angular';
import * as moment from 'moment';
import { ApiService } from 'src/app/_services/api.service';

@Component({
  selector: 'app-groups',
  templateUrl: './groups.page.html',
  styleUrls: ['./groups.page.scss'],
  providers: [ApiService]
})
export class GroupsPage implements OnInit {

  subscribe: any;

  lists = [];

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    public NavController: NavController,
    private apiService: ApiService,
    public alertController: AlertController,
    public actionSheetController: ActionSheetController
  ) { }

  ngOnInit() {
  }

  ionViewDidEnter() {
    this.getListActiveGroup();
  }

  async presentAlertNoQuestion() {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Info !',
      message: 'Belum ada pertanyaan yang diterbitkan',
      mode: "ios",
      buttons: [
        {
          text: 'Tutup',
          role: 'cancel',
          cssClass: 'secondary'
        }
      ]
    });

    await alert.present();
  }

  async presentActionSheetSelectPeriode(item: any) {

    if (item.Q_Mingguan.length == 0 &&
      item.Q_Bulanan.length == 0 &&
      item.Q_Triwulan.length == 0) {

      this.presentAlertNoQuestion();

    } else {

      let buttons = [];

      if (item.Q_Mingguan.length > 0) {
        buttons.push({
          text: 'Mingguan',
          handler: () => {
            this.gotoSelectPeriode(item, 'Mingguan');
          }
        });
      }

      if (item.Q_Bulanan.length > 0) {
        buttons.push({
          text: 'Bulanan',
          handler: () => {
            this.gotoSelectPeriode(item, 'Bulanan');
          }
        });
      }

      if (item.Q_Triwulan.length > 0) {
        buttons.push({
          text: 'Triwulan',
          handler: () => {
            this.gotoSelectPeriode(item, 'Triwulan');
          }
        });
      }

      buttons.push({
        text: 'Tutup',
        role: 'cancel',
        handler: () => {
          // console.log('Cancel clicked');
        }
      });

      console.log('buttons', buttons);

      const actionSheet = await this.actionSheetController.create({
        header: 'Pilih Periode',
        cssClass: 'my-custom-class',
        mode: "ios",
        buttons: buttons
      });
      await actionSheet.present();

    }


  }

  async presentAlertSelectTarget(id_questionnaire_desa, id_questionnaire_dusun) {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Target Laporan',
      mode: "ios",
      message: 'Pilih target pengisian',
      buttons: [
        {
          text: 'Desa',
          cssClass: 'specialClass',
          handler: async () => {

            try {
              let dataParam = await this.getPeriode(id_questionnaire_desa);
              this.NavController.navigateForward(['/laporan/selectperiode', dataParam[0]]);
            } catch (error) {
              if (error.status == 401) {
                this.alertToRelogin();
              }
            }

          }
        }, {
          text: 'Dusun',
          handler: async () => {
            try {
              let dataParam = await this.getPeriode(id_questionnaire_dusun);
              this.NavController.navigateForward(['/laporan/selectperiode', dataParam[0]]);
            } catch (error) {
              if (error.status == 401) {
                this.alertToRelogin();
              }
            }

          }
        }
      ]
    });

    await alert.present();
  }

  async alertToRelogin() {

    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Warning !',
      message: 'Akun anda terdeteksi sedang digunakan diperangkat lain, mohon login kembali untuk mendapatkan akses Anda kembali',
      mode: "ios",
      backdropDismiss: false,
      buttons: [
        {
          text: 'Login Ulang',
          handler: () => {

            localStorage.removeItem('currentUser');
            localStorage.removeItem('geo_id');
            localStorage.removeItem('api_key');
            this.router.navigate(['front/']);

          }
        }
      ]
    });

    await alert.present();
  }

  getListActiveGroup() {
    this.subscribe = this.apiService.get('v2/report_active_group', {}).subscribe(
      data => {
        this.lists = data;
        console.log(this.lists);
      },
      error => {
        if (error.status == 401) {
          this.alertToRelogin();
        }
      }
    );
  }

  async gotoSelectPeriode(item: any, type: string) {

    if (item['Q_' + type].length > 1) {

      let id_questionnaire_desa = '';
      let id_questionnaire_dusun = '';

      for (let index = 0; index < item['Q_' + type].length; index++) {
        const element = item['Q_' + type][index];
        if (element.target == 0) {
          id_questionnaire_dusun = element.id;
        } else if (element.target == 1) {
          id_questionnaire_desa = element.id;
        }

      }

      this.presentAlertSelectTarget(id_questionnaire_desa, id_questionnaire_dusun);

    } else {
      let id_questionnaire = item['Q_' + type][0].id;
      try {
        let dataParam = await this.getPeriode(id_questionnaire);
        this.NavController.navigateForward(['/laporan/selectperiode', dataParam[0]]);
      } catch (error) {
        if (error.status == 401) {
          this.alertToRelogin();
        }
      }

    }

  }

  async getPeriode(id_questionnaire): Promise<any> {

    return new Promise(async (resolve, rejected) => {

      try {

        this.subscribe = this.apiService.get('v2/report_show_periode?id_questionnaire=' + id_questionnaire, {}).subscribe(
          data => {
            resolve(data);
          },
          error => {
            rejected(error);
          }
        );

      } catch (err) {
        rejected(err);
      }

    });




  }

  getMonthName(number) {
    return moment().month((parseInt(number) - 1)).format('MMMM');
  }

  gotoDataDasar() {
    // this.NavController.navigateBack(['/home'], { replaceUrl: true });
    this.NavController.navigateForward(['/laporan/datadasardesa', []]);
  }

  goBack() {
    this.NavController.navigateBack(['/home'], { replaceUrl: true });
  }

}
